<template>
 <div>
     <div v-for="(fashion,i) in shopcityData">
     <div class="fash" v-for="(f,i) in fashion.fashion">
    <h3>{{f.type}}</h3>
    <ul class="fash-shopping"> 
         <li  v-for="(pro,i) in f.fashionPro">
            <a href="#">
                <h5>{{pro.proName}}</h5>
                <img :src="pro.proImg">
            </a>
        </li>
    </ul>
  </div>
 </div>
 </div>
</template>

<script>
 export default {
   data () {
     return {
         name:'shopCityFash'
     }
   },
   props:["shopcityData"]
 }
</script>

<style>
.fash{
    background:white;
}
.fash h3{
    font-size:.18rem;
    color:#676767;
    padding-top:.31rem;
    padding-bottom:.31rem;
    margin-left: .09rem;
}
.fash-shopping{
    display: flex;
    width:100%;
    flex-wrap: wrap;
}
.fash-shopping li{
    width:1.13rem;
    height:1.13rem;
    background:#f3f3f3;
    margin-left: .09rem;
    margin-bottom:.09rem;
    border-radius: 3px;
}
.fash-shopping li h5{
    font-size: 14px;
    color:black;
    text-align: center;
    margin-top:.21rem;
    margin-bottom:5px;
}
.fash-shopping li img{
    widows: 0.8em;
    height:.6rem;
    display: block;
    margin:0 auto;
}
 
</style>



