<template>
 <div class="head">
     <ul v-for="(d,index) in detailsData">
         <li >
            <img v-for="(img,index) in d.Img" :src="img.img">
         </li>
         <div class="left"><</div>
          <div class="right"><img src="../../assets/img/car2.png"></div>
     </ul>
     <div class="head-bottom" v-for="(a,index) in detailsData">
         <h3><span>￥</span>{{a.price}}</h3>
         <p><span>￥{{a.price1}}</span><img src="../../assets/img/zhe.jpg"></p>
     </div>
     <p v-for="(b,index) in detailsData" class="info">{{b.infos}}<span>免邮费</span></p>
 </div>
</template>

<script>
 export default {
   data () {
     return {
         name:'detailsHeader'
     }
   },
   props:["detailsData"]
 }
</script>

<style>
.head{
background:white;
}
.head ul{
    width:100%;
    position:relative;
    margin-top:10px;  
}
.head ul li{
    display:flex;
    width:2.6rem;
    flex-wrap:nowrap;
    overflow-x:auto;
    flex:1;
    margin:0 auto;
}
.head ul li img{
    flex-shrink:0;
    width:2.6rem;
    height:3.3rem;
}
.left{
    width:.39rem;
    height:.39rem;
    border-radius:50%;
    text-align:center;
    line-height:.39rem;
    background:rgba(25,23,20,.6);
    color:white;
    font-size:.25rem;
    position:absolute;
    left:10px;
    top:10px;
} 
.right{
     width:.39rem;
    height:.39rem;
    border-radius:50%;
    text-align:center;
    line-height:.39rem;
    background:rgba(4,6,1);
    opacity:.5;
     position:absolute;
    right:10px;
    top:10px;
}
.right img{
    width:.2rem;
    height:.2rem;
    margin:0 auto;
    padding:10px;
}
.head-bottom{
    display:flex;
    margin-left:.2rem;
      margin-top:5px;
}
.head-bottom h3{
    font-size:.3rem;
    color:#2b2b2b;
    font-weight:normal;
}
.head-bottom h3 span{
     font-size:.12rem;
}
.head-bottom p span{
    font-size:.12rem;
    color:#b0b0b0;
    text-decoration:line-through;
     display:block;
     margin-top:5px;
}
.head-bottom p{
    margin-left:10px;
}
.head-bottom p img{
     width:.3rem;
    height:.2rem;
    display:block;
     margin-top:5px;
}
.info{
    font-size:.16rem;
    width:3.32rem;
    margin:0 auto;
}
.info span{
    font-size:.12rem;
    color:#b0b0b0;
     display:block; 
      margin-top:10px;
     padding-bottom:10px;
}
</style>



