<template>
 <div>
<div class="content">
            <ul>
               <li v-for="(t,index) in indexData" @click="change(index)" :class="{'find':index==c}"> {{t.type}}</li> 
            </ul>
            <div class="more"  v-for="(c,index) in indexData[num].indexCon">
                <div class="more-top">
                    <img :src="c.touxiang">
                    <div class="more-top-right">
                        <h3>{{c.name}}</h3>
                        <div class="info">
                            <p>178cm59kg &nbsp;|&nbsp;</p>
                            <p>{{c.city}} &nbsp;|&nbsp;</p>
                            <p>{{c.work}}</p>
                        </div>
                    </div>
                </div>
                <div class="word">
                    <p>{{c.speak}}</p>
                    <p>{{c.speak}}</p>
                </div>
                <div class="more-center">
                    <img :src="c.photo.photo1">
                    <img :src="c.photo.photo2">
                    <img :src="c.photo.photo3">
                </div>
                 <div class="one_bottom">
                <ul>
                    <li v-for="(br,index) in c.brands">
                        <a href="JavaScript:;">
                        <img :src="br.bimg" alt>
                        <span>{{br.bspan}}</span>
                        </a>
                     </li>
            </ul>
            </div>
            </div>
        </div>
 </div>
</template>

<script>

 export default {
    name:"indexContent",
    props:["indexData","change","c","num"],

}
</script>

<style >
.content{
    width:100%;
    background:white;
}
.content ul{
    display: flex;
    width:100%;
    height:.5rem;
    border-bottom:1px solid #e9e9e9;
    align-items: center;
}
.content ul li{
    list-style:none;
    height:.5rem;
    line-height:.5rem;
    font-size: .15rem;
    color:black;
    font-weight:900;
    margin-left: .78rem;
}
.content ul .find{
    border-bottom:2px solid #ff3e79;
  }
.more{
   width:100%; 
}
.more-top{
    display: flex;
    height:.7rem;
    align-items: center;
}
.more-top img{
    width:.4rem;
    height:.4rem; 
    border-radius: 50%;
    margin-left:.2rem;
    margin-right:.2rem;
}
.more-top-right h3{
 font-size:.17rem;
 margin-bottom:5px;
}
.info{
    display: flex;
}
.info p{
    font-size:.12px;
    color:#8a8a8a;
}
.info p span{
    color:black;
    font-weight:900;
}
.more-center{
    display: flex;
    justify-content: space-between;
}
.more-center img{
    width:1.235rem;
    height:2rem;
}
.word p{
    width:3.5rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 14px;
    color:#242424;
    margin:0 auto;
    margin-bottom:2px;
}
.one_bottom{
    width:3.75rem;
    background:white;
    display:flex;
    line-height: .45rem;
  }
.one_bottom ul{
    line-height: .2rem;
    margin:0 .1rem .2rem;
    border-bottom:1px solid #ddd;
    width:100%;
    justify-content: flex-start;
  }
  .one_bottom ul li{
    background:#f3f3f7;
    margin:.13rem .05rem .13rem 0;
    font-weight: 100;
    text-align: center;
    padding: .02rem .1rem;
    box-sizing: border-box;
    border-radius: 4px;
  }
 .one_bottom ul li a img{
    width:.15rem;
    height:.15rem;
    display:inline-block;
    border:1px solid #e5e5e5;
    border-radius: 3px;
    vertical-align: middle;
  }
  .one_bottom ul li a span{
    font-size:.1rem;
    color: #666666;
  }


 
</style>



