<template>
 <div class="box">
     <index-banner></index-banner>
     <index-nav></index-nav>
     <index-content :indexData="indexData" :change="change" :c="c" :num="num"></index-content>
 </div>
</template>

<script>
import indexBanner from "./indexBanner"
import indexNav from "./indexNav"
import indexContent from "./indexContent"
 export default {
   name:'indexSection',
   data () {
     return {
      num:0,
      c:0
     }
   },
   components: {
       indexBanner,
       indexNav,
       indexContent
   },
   props:["indexData"],
  methods:{
    change(index){
      this.num=index;
      this.c=0;
      this.c=index;
    }
  }
}
</script>

<style >
.box{
   width:100%;
    flex:1;
    overflow-y: auto;
    display:flex;
   flex-direction: column;
}

 
</style>



