<template>
 <div class="box">
     <details-header :detailsData="detailsData"></details-header>
     <details-chose></details-chose>
     <details-shop></details-shop>
 </div>
</template>

<script>
import detailsHeader from "./detailsHeader"
import detailsChose from "./detailsChose"
import detailsShop from "./detailsShop"
 export default {
   data () {
     return {
         name:'detailsSection'
     }
   },
   components: {
       detailsHeader,
       detailsChose,
       detailsShop
   },
   props:["detailsData"]
 }
</script>

<style>
.box{
   width:100%;
    flex:1;
    overflow-y: auto;
}
 
</style>



