<template>

     <div class="class-list" >
         <ul>
             <li  v-for="(a,index) in classifyData" @click="change(index)" :class="{'first-li':index==c}">{{a.type}}</li>
         </ul>
     </div>
 
</template>

<script>
 export default {
   data () {
     return {
         name:'classifyLeft'
     }
   },
    props: ["classifyData", "change", "c"]
 }
</script>

<style>
.class-list ul{
    width:1rem;
    height:100%;
    overflow-y:auto;
    background:#f6f6f6;
}
.class-list ul li{
    font-size:.14rem;
    width:100%;
    height:.6rem;
    text-align:center;
    line-height:.6rem;
    color:#6b6b6b;
}
 .class-list ul .first-li{
    background:white;
    color:#f55d74;
    font-weight:900;
} 
</style>



