<template>
 <div id="content">
<div class="content" v-for="(z,i) in zhiboData[num].zhibouer">
        <div class="zhibo-main" v-for="(zb,i) in z.zhiboBox">
            <div class="main-center" :style="{backgroundImage: 'url(' + zb.bImg + ')',backgroundSize:'3.5rem 2.01rem'}">
                <div class="main-top">
                    <img :src="zb.userImg" alt="">
                    <p>{{zb.personname}}</p>
                </div>
                <div class="zhiboing">
                    <div class="circle"></div>
                    <span>直播中</span>
                    <span>|</span>
                    <span>🔥{{zb.redu}}</span>
                </div>
                <h3>{{zb.zhibotit}}</h3>
                <div class="main-bottom">
                    <div class="main-botton-img1"  v-for="(d,i) in zb.dd" :style="{backgroundImage: 'url(' + d.ddImg + ')',backgroundSize:'.61rem .61rem'}">
                        <span>{{d.ddspan}}</span>
                    </div>
                    <div class="main-bottom-right">
                        <p v-for="(b,i) in zb.zhibopinglun"><img :src="b.pinglunImg" alt="">{{b.pinglunspan}}</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="new-zhibo" v-for="(zn,i) in z.newshow">
            <h3>{{zn.newTit}}</h3>
            <div class="new">
            <div class="new-zhibo-mg1" v-for="(zp,i) in zn.newPer" :style="{backgroundImage:'url('+zp.newshowImg+')', backgroundSsize: '1.15rem 1.15rem'}">
                <div class="zhibo-z">直播在</div>
                <p>{{zp.newshowTit}}</p>
            </div>
        </div>
        </div>
    </div>
 </div>
</template>

<script>
 export default {
   data () {
     return {
         name:'zhiboSection'
     }
   },
     props: ["zhiboData", "num"]
 }
</script>

<style>
#content {
    overflow-x: hidden;
    overflow-y: auto;
    flex: 1;
  }
.circle{
    width:8px;
    height:8px;
    border-radius:50%;
    background:red;
    margin-top:.05rem;
}
.zhibo-main{
    width:100%;
   
}
.main-center{
    width:3.5rem;
    height:2.01rem;
    margin:0 auto;
    margin-top:.5rem;
    position: relative;
    border-radius: 5px;
}
.main-top{
    display: flex;
    align-items: center;
    position: absolute;
    left:17px;
    top:-38px;
}
.main-top img{
    width:.49rem;
    height:.46rem;
    border-radius: 50%;
}
.main-top p{
    font-size: .14rem;
}
.zhiboing{
    width:1.13rem;
    height:.19rem;
    background:rgba(12, 13, 13, .4);
    display: flex;
    border-radius: 5px;
    position: absolute;
    left:15px;
    top:77px;
    line-height: .19rem;
}
.zhiboing span{
    font-size: .12rem;
    color:white;
    margin-left:7px;
}
.main-center h3{
    font-size: .17rem;
    color:white;
    position: absolute;
    left:15px;
    top:100px;
}
.main-bottom{
    display: flex;
    width:3.3rem;
    position: absolute;
    left:15px;
    top:130px;
}
.main-botton-img1{
    width:.61rem;
    height:.61rem;
    margin-right: 8px;
    position: relative;
}
.main-botton-img1 span{
    font-size: .14rem;
    color:white;
    display: block;
   position: absolute;
   left:5px;
   bottom:5px;
}
.main-bottom-right{
    margin-left: 10px;
}
.main-bottom-right p{
    font-size: .14rem;
    color:white;
    display: flex;
    width:1rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.main-bottom-right p img{
    width:.12rem;
    height:.12rem;
    border-radius: 50%;
}
.new{
    display: flex;
    justify-content: space-around;
    margin-top:.15rem;
}
.new-zhibo h3{
    font-size: .16rem;
    margin-top:.15rem;
    margin-left: 10px;
}
.new-zhibo-mg1{
    width:1.15rem;
    height:1.15rem;
    border-radius: 5px;
}
.zhibo-z{
    width:.55rem;
    height:.2rem;
    background:rgba(12, 13, 13, .5);
    font-size: .1rem;
    color:white;
    border-radius: 3px;
    text-align: center;
    line-height: .2rem;
    margin-top:.5rem;
    margin-left: 10px;
}
.new-zhibo-mg1 p{
    font-size: .1rem;
    color:white;
    width:1rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-top:5px;
    margin-left: 10px;
}
 
</style>



