<template>
 <div class="box">
    <classify-left :classifyData="classifyData" :change="change" :c="c"></classify-left>
    <classify-right :classifyData="classifyData" :num="num"></classify-right>
 </div>
</template>

<script>
import classifyLeft from "./classifyLeft"
import classifyRight from "./classifyRight"
 export default {
   data () {
     return {
         name:'classifySection',
         num:0,
         c:0
     }
   },
   methods:{
       change(index){
           this.num=index;
           this.c=0;
           this.c=index;
       }
   },
   components: {
       classifyLeft,
       classifyRight
   },
   props:["classifyData"]
 }
</script>

<style>
.box{
    width:100%;
    height:100%;
    display:flex;
}
 
</style>



