<template>
  <footer>
    <div class="sec_t_l">
        <span class="Spans" v-for="(a,i) in shopCarData">
          <input type="checkbox" class="input_check" @change="CheckAll()" v-model="a.allchecked" >
          <label></label>
        </span>
      <span class="quanxuan">全选</span>
      <span class="zongjia">￥{{zj}}</span>
      <span class="jiezhang">去结算</span>
    </div>
  </footer>
</template>

<script>
  export default {
    name: "shopcartFooter",
     props: ["shopCarData","zj","CheckAll"],
    // methods:{,"allchecked","changeAllCheck"
    //   change(){
    //     this.changeAllCheck()
    //     this.$emit("proChecked");
    //   }
    // }
  }
</script>

<style>
  footer {
    border-top: 1px solid #d8d8d8;
    padding: 0 .15rem;
    box-sizing: border-box;;
    height: .5rem;
    width: 100%;
    background: white;
    position: fixed;
    bottom: 0;
  }

  .sec_t_l {
    display: flex;
    justify-content: start;
    height: .45rem;
    align-items: center;
    position: relative;
  }

  .sec_t_l span {
    color: #5e5e5e;
    font-size: .14rem;
    line-height: .14rem;
    margin-right: .15rem;
  }

  .input_check {
    opacity: 0;
    width: .2rem;
    height: .2rem;
    position: absolute;
  }

  label {
    display: inline-block;
    width: .2rem;
    height: .2rem;
    background: url("../../assets/img/checkbox.png") 0 100% no-repeat;
    background-size: .2rem auto;
    /*position: absolute;*/
  }

  .input_check:checked + label {
    background: url("../../assets/img/checkbox.png") 0 0 no-repeat;
    background-size: .2rem auto;
  }

  .quanxuan {
    font-size: .14rem;
    line-height: .5rem;
  }

  .sec_t_l .zongjia {
    font-size: .14rem;
    line-height: .5rem;
    color: #ee4566;
  }

  .sec_t_l .jiezhang {
    font-size: .16rem;
    float: right;
    display: block;
    color: white;
    background: #ff5777;
    box-sizing: border-box;
    padding: 0.12rem 0.25rem;
    border-radius: 0.08rem;
    vertical-align: middle;
    margin: 0.08rem 0.12rem;
    cursor: pointer;
    min-width: 1rem;
    text-align: center;
    position: absolute;
    left: 2.2rem;
    top: -0.03rem;
  }
</style>



