<template>
 <div class="swiper">
     <div class="banner">
        <!-- Swiper -->
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide"><img src="../../assets/img/1.jpg"></div>
        <div class="swiper-slide"><img src="../../assets/img/2.jpg"> </div>
        <div class="swiper-slide"><img src="../../assets/img/3.jpg" ></div>
        <div class="swiper-slide"><img src="../../assets/img/4.jpg" ></div>
        <div class="swiper-slide"><img src="../../assets/img/5.jpg" ></div>
        <div class="swiper-slide"><img src="../../assets/img/6.jpg"> </div>
      </div>
      <!-- Add Pagination -->
      <div class="swiper-pagination"></div>
    </div>
    </div>
 </div>
</template>

<script>
    import  $ from "jquery"
    import Swiper from "swiper"
    export default {
        name: "IndexSwiper",
        mounted(){
          var swiper = new Swiper('.swiper-container', {
            pagination: {
              el: '.swiper-pagination',
            },
            autoplay:true,
            loop:true
          });
       }
    }

</script>

<style>
@import "../../../node_modules/swiper/dist/css/swiper.min.css";
.swiper{
  width:100%;
}
  .banner{
    width: 100%;
    height: 1.5rem;
  }
  .swiper-container {
    width: 100%;
    height: 100%;
  }
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
  .swiper-slide img{
    width: 100%;
    height: 100%;
  }
  .swiper-container-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet{
    margin: -29px 4px;
  }
  .swiper-pagination-bullet{
    background:white;
    opacity:1;
  }
  .swiper-pagination-bullet-active{
    background:#d53e2d;
  }
</style>



