<template>
 <div>
<div class="head-word">
            <ul>
                <li>
                    <img src="../../assets/img/m9.jpg" alt="">
                    <span>我的钱包</span>
                     <span>></span>
                </li>
                <li>
                    <img src="../../assets/img/m10.jpg" alt="">
                    <span>收货地址</span>
                    <span>></span>
                </li>
                <li>
                    <img src="../../assets/img/m11.jpg" alt="">
                    <span>客服</span>
                    <span>></span>
                </li>
                <li>
                    <img src="../../assets/img/m12.jpg" alt="">
                    <span>QQ服务号</span>
                    <span>></span>
                </li>
            </ul>
            <a href="#" class="end">退出登录</a>
        </div>
 </div>
 </div>
</template>

<script>
 export default {
   data () {
     return {
         name:'myBottom'
     }
   },
   components: {

   }
 }
</script>

<style>
.head-word{
    margin-top:10px;
    background:white;
}
.head-word ul li{
    display: flex;
    align-items: center;
    margin-left: .2rem;
    width:3.55rem;
    height:.56rem;
    border-bottom:1px solid #d3d3d3;
    position:relative;
}
.head-word ul li:last-of-type{
    border-bottom:none;
}
.head-word ul li img{
    width:.19rem;
    height:.19rem;
    margin-left: 10px;
}
.head-word ul li span{
    font-size: .16rem;
    color:#696969;
    margin-left: 10px;
}
 .head-word ul li span:nth-of-type(2){
     position:absolute;
     right:10px;
     top:15px;
 }
 .end{
     width:3.3rem;
     height:.4rem;
     background:#ff5778;
     color:white;
     font-size:.16rem;
     text-align:center;
     line-height:.4rem;
     display:block;
     margin:.24rem auto;
 }
</style>



