<template>
 <div>
     <ul class="buy">
       <a href="#"><li><img src="../../assets/img/p1-1.png" alt=""><span>风尚快抢</span></li></a>
       <a href="#"><li><span>热销榜单</span></li></a>
       <a href="#"><li><span>好货精选</span></li></a>
       <a href="#"><li><span>新品快抢</span></li></a>
   </ul>
 </div>
</template>

<script>
 export default {
   data () {
     return {
       name:'shopCityBuying'
     }
   }
 }
</script>

<style>
.buy{
    display: flex;
    width:100%;
    background:white;
}
.buy li{
    list-style: none;
    width:.81rem;
    height:.82rem;
    margin-left: .1rem;
    margin-top:.18rem;
    margin-bottom:.21rem;
    position: relative;
}
.buy a:nth-child(1) li{
    background:url(../../assets/img/p1.png);
}
.buy a:nth-child(2) li{
    background:url(../../assets/img/p2.png);
    background-size: cover;
}
.buy a:nth-child(3) li{
    background:url(../../assets/img/p3.png);
    background-size: cover;
}
.buy a:nth-child(4) li{
    background:url(../../assets/img/p4.jpg);
    background-size: cover;
}
.buy li img{
    width:.61rem;
    height:.82rem; 
    margin:0 auto;
    display: block;
}
.buy li span{
    font-size: .14rem;
    font-weight:900;
    color:#fbffff;
    display: block;
    position:absolute;
    left:11px;
    bottom:1px;
}
 
</style>



