    registerCent
   }
 }
</script>

<style>
@import "../assets/css/zhibo.css";
.bigBox{
         width:100%;
        height:100%;
        display:flex;
        flex-direction: column;
        background:white;
    }
 .bigBox .centent{
          flex:1;
          overflow-y:auto;
         background:white;
        }
</style>



