<template>
 <div class="head">
     <ul>
         <li>
             <a href="#"><</a>
         </li>
          <li>
             购物车()
         </li>
         <li>
             <a href="#"><span>编辑</span><img src="../../assets/img/info.jpg"></a>
         </li>
     </ul>
 </div>
</template>

<script>
 export default {
   data () {
     return {
         name:'shopCarHeader'
     }
   },
   components: {

   }
 }
</script>

<style>
.head{
 width:100%;
 background:white;
}
.head ul{
     width:100%;
     height:.44rem;
    display:flex;
     align-items:center;
   justify-content:space-between;
   border-bottom:1px solid #e1e1e1;
}
.head ul li:nth-child(1){
      font-size:.25rem;
      margin-left:.2rem;
}
.head ul li:nth-child(3){
      margin-right:.2rem;
}
.head ul li{
    font-size:.18rem;
}
.head ul li a{
    display:flex;
    align-items:center;
    color:#595959;
}
.head ul li span{
    font-size:.14rem;
    display:block;
}
.head ul li a img{
    width:.2rem;
    height:.2rem;
    margin-left:10px;
}
</style>



