<template>
 <div class="shop">
     <div class="shop-top">
         <img class="ms" src="../../assets/img/shop.jpg">
         <div class="name">
             <p>胖mm靚装</p>
             <div class="xin">
                 <img src="../../assets/img/xin.jpg">
                  <img src="../../assets/img/xin.jpg">
                   <img src="../../assets/img/xin.jpg">
             </div>
         </div>
         <a href="#">进店 ></a>
     </div>
      <ul class="shop-bottom">
         <li>
             <p>21.8万</p>
             <span>总销量</span>
         </li>
         <li>
             <p>18134</p>
             <span>收藏数</span>
         </li>
         <li>
             <p class="p"> <span>描述相符</span><span>4.49</span></p>
             <p class="p"><span>质量满意</span><span>4.49</span></p>
         </li>
     </ul>
 </div>
</template>

<script>
 export default {
   data () {
     return {
         name:"detailsShop"
     }
   },
   components: {

   }
 }
</script>

<style>
.shop{
background:white;
}
.shop-top{
    display:flex;
    margin-top:10px;
    align-items:center;
}
.shop-top .ms{
    width:.48rem;;
    height:.48rem;
    margin-left:.16rem;
} 
.name{
     margin-left:10px; 
}
.name p{
    font-size:.14rem;
    color:;
}
.xin{
    display:flex; 
}
.xin img{
   width:10px;
    height:10px; 
}
.shop-top a{
    width:.62rem;
    height:.3rem;
    border:1px solid #cd5a79;
    color:#cd5a79;
    font-size:.14rem;
    text-align:center;
    line-height:.3rem;
    border-radius:5px;
    display:block;
    margin-left:1.5rem;
}
.shop-bottom{
     display:flex;
     width:100%;
     margin-top:10px;
    padding-bottom:10px;
}
.shop-bottom li{
    width:2.2rem;
    height:.4rem;
    text-align:center;
}
.shop-bottom li:nth-of-type(2){
      width:1.16rem;
       height:.4rem;
    border-right:1px solid #a1a4a4;
    border-left:1px solid #a1a4a4;
}
.shop-bottom li p{
    font-size:.16rem;
}
.shop-bottom li span{
     font-size:.12rem;
     color:#a1a4a4;
     display:block;
}
.p{
   display:flex;
   align-items:center; 
   justify-content:center; 
}
.p span{
    margin-top:3px;
}
.p span:nth-of-type(2){
    color:green;
    margin-left:5px;
}
</style>



