<template>

     <div class="righti-nfo">
         <ul >
             <li v-for="(r,index) in  classifyData[num].goods">
                <router-link to="goodsList">
                    <img :src="r.goodsImg">
                    <span>{{r.goodsSpan}}</span>
               </router-link>
             </li>
         </ul>
     </div>

</template>

<script>
 export default {
   data () {
     return {
        name:'classifyRight'
     }
   },
  props:["classifyData","num"]
 }
</script>

<style>
.righti-nfo{
    width:2.65rem;
    margin-left:.1rem;
    margin-top:.2rem;
    overflow-y:auto;
}
.righti-nfo ul{
    height:100%;
     width:2.65rem;
    display:flex;
     flex-wrap: wrap;
    
}
.righti-nfo ul li {
    margin:.1rem;
}
.righti-nfo ul li a img{
    width:.65rem;
    height:.65rem;
}
.righti-nfo ul li a span{
    font-size:.14rem;
    color:#656565;
    display:block;
    text-align:center;
    margin-top:5px;
}
 
</style>



