<template>
 <div class="bigBox">
     <details-section :detailsData="detailsData"></details-section>
     <details-footer></details-footer>
 </div>
</template>

<script>
import detailsSection from "../components/details/detailsSection"
import detailsFooter from "../components/details/detailsFooter"
 export default {
   data () {
     return {
         name:'detailsSection',
         detailsData:[
             {
                 "Img":[
                     {
                         "img":"static/img/a1.jpg"
                     },
                     {
                         "img":"static/img/a2.jpg"
                     },
                     {
                         "img":"static/img/a3.jpg"
                     },
                     {
                         "img":"static/img/a4.jpg"
                     }
                 ],
                 "price":"69.30~139.30",
                 "price1":"99.00起",
                 "infos":"微胖妹妹春装显瘦洋气套装2019大码女装胖mm阔腿裤时髦心机两件套"
             }
         ]
     }
   },
   components: {
       detailsSection,
       detailsFooter
   }
 }
</script>

<style>
@import "../assets/css/index.css";
.bigBox{
    width:3.75rem;
    height:100%;
    display:flex;
    flex-direction: column;
}
 
</style>



