<template>
<div>
    <div class="shop-list" v-for="(sh) in shopCarData">
        <div class="all"  v-for="(s,index) in sh.shop">
            <div class="shop-list-top">
                <span>
                    <input type="checkbox" class="check"  @change="shopCheckAll(index)" v-model="s.checked">
                    <label></label>
                </span>
                <span>{{s.shopname}}</span>
            </div>
            <div class="shop-list-goods" v-for="(g) in s.product">
                <div class="goods">
                    <span>
                        <input type="checkbox" class="check" v-model="g.check">
                        <label></label>
                    </span>
                </div>  
                <img :src="g.Img"> 
                <div class="info">
                    <h3>{{g.Info}}</h3>
                    <span>颜色：{{g.color}}；尺码：{{g.size}}；</span>
                    <div class="bt">
                        <p>￥{{g.price}}<span>{{g.yprice}}</span></p>
                        <span class="spans">x{{g.num}}</span>
                    </div>
                </div>
            </div>
       </div>
    </div>
 </div>
</template>

<script>
 import $ from "jquery";
 export default {
   data () {
     return {
         name:'shopList'
     }
   },
   props: ["shopCarData","shopCheckAll"],
 }
</script>
<style>
.shop-list{
    position: relative;
    margin-bottom:1rem;
}
.all{
     background:white;
    margin-top:10px;
}
.shop-list-top{
    display:flex;
    width:100%;
    height:.37rem;
    border-bottom:1px solid #e1e1e1;
    align-items:center;
}
.shop-list-top .check{
    opacity: 0;
    width: .2rem;
    height: .2rem;
    position: absolute;
    margin-left:.2rem;
} 
.shop-list-top span{
    font-size:.16rem;
    color:#515151;
    margin-left:10px;
}
label {
    display: inline-block;
    width: .2rem;
    height: .2rem;
    background: url("../../assets/img/checkbox.png") 0 100% no-repeat;
    background-size: .2rem auto;
  }
.check:checked + label {
    background: url("../../assets/img/checkbox.png") 0 0 no-repeat;
    background-size: .2rem auto;
  }
.shop-list-goods{
      width:100%;
       display: flex;
       margin-top:.08rem;
  }
.goods{
    display: flex;
    justify-content: start;
    height: .7rem;
    align-items: center;
    position: relative;
}
.goods span{
    color: #5e5e5e;
    font-size: .14rem;
    line-height: .14rem;
    margin-left: .1rem;
   
}
.goods .check{
     opacity: 0;
    width: .2rem;
    height: .2rem;
    position: absolute;
    margin-left:.2rem;
}
label {
    display: inline-block;
    width: .2rem;
    height: .2rem;
    background: url("../../assets/img/checkbox.png") 0 100% no-repeat;
    background-size: .2rem auto;
  }
.check:checked + label {
    background: url("../../assets/img/checkbox.png") 0 0 no-repeat;
    background-size: .2rem auto;
  }
.shop-list-goods img{
     width:.72rem;
     height:.96rem;
     margin-left:10px;
 }
 .info h3{
     font-size:.14rem;
     width:2.2rem;
     height:.4rem;
     overflow:hidden;
     margin-left:.15rem;
     color:#6a6a6a;
 }
 .info span{
     font-size:.12rem;
     display:block;
    color:#929292;
     margin-left:.15rem;
 }
 .bt{
    display:flex; 
    width:100%;
     margin-left:.15rem;
     margin-top:.26rem;
 }
 .bt p span{
    font-size:.12rem;
    display:block;
    color:#929292;
    text-decoration: line-through;
 }
 .bt p{
     font-size:.16rem;
      display:flex;
      align-items:center;
       color:#6a6a6a;
 }
 .bt .spans{
     margin-left:1rem;
 }
</style>



