<template>
    <div class="cent">
        <form>
            <input type="text" placeholder="请输入电话号码"/>
            <p>
                <input type="text" placeholder="请输入验证码"/>
                <span>获取验证码</span>
            </p>
            <input type="text" placeholder="请输入用户名"/>
            <input type="text" placeholder="请输入昵称"/>
            <input type="password" placeholder="请输入密码"/>
            <input type="password" placeholder="请再次输入密码"/>
            <div class="ra"><input class="inpra" type="radio"/>同意注册协议</div>
            <input class="btn" type="button" value="注册"/>
        </form>
    </div>
</template>

<script>
 export default {
     name:"registerCent",
   data () {
     return {

     }
   },
   components: {

   }
 }
</script>

<style>
.cent{
        width:100%;    
        }
 .cent form{
            width:100%;
            display: flex;
            flex-direction: column;
 }
 .cent form input{
                display: block;
                height:.6rem;
                font-size: 14px;
                color:#91908f;
                
                border:none;
                width:100%;
                text-indent:.2rem;
                border-bottom:1px solid #e0e0e2;
                outline: 0;
            }
  .cent form p{
                position: relative;
            }
   .cent form p span{
                    position: absolute;
                    top:.1rem;
                    right:0.2rem;
                    display: block;
                    background:#f95778;
                    width:1rem;
                    height:.4rem;
                    border-radius: .05rem;
                    font-size: 12px;
                    color:white;
                    line-height: .4rem;
                    text-align: center;
                }
 .cent form .btn{
                margin:0 auto;
                width:1.5rem;
                height:.5rem;
                background:#f95778;
                border:1px solid #e3008d;
                border-radius: .05rem;
                font-size:.16rem;
                color:white;
                text-align: center;
                margin-top:.2rem;
            }    
 .cent form .ra{
                display: flex;
                font-size: .14rem;
                align-items: center;
                margin:0 auto;
                margin-top:1rem;
             }      
 .ra .inpra{
                display:flex;
                width:.2rem;
                height:.2rem;
                margin-right:.05rem;
            } 
</style>



