<template>
 <div>
<div class="heads">
            <img src="../../assets/img/info.jpg" alt="">
            <div class="search">
                <img src="../../assets/img/big.jpg" alt="">
                <span>外套</span>
            </div>
            <img src="../../assets/img/car.jpg" alt="">
        </div>
 </div>
</template>

<script>
 export default {
   data () {
     return {
         name:'classifyHeader'
     }
   },
   components: {

   }
 }
</script>

<style>
.heads{
    background:white;
    width:100%;
    height:.43rem;
    display: flex;
    align-items: center;
    border-bottom:1px solid #e6e6e6;
}
.heads img{
    width:.25rem;
    height:.22rem;
    margin-left: .18rem;
    margin-right: 10px;
}
.search{
    display: flex;
    width:2.64rem;
    height:.27rem;
    background:#eeeeee;
    border-radius: 3px;
    align-items: center;
}
.search img{
    width:.16rem;
    height:.18rem;
}
.search span{
    font-size:12px;
    color:#979797;
    display: block;
}
 
</style>



