<template>
 <div>
<div class="like-shopping" v-for="(like,i) in shopcityData">
    <div class="like">
            <div class="like-top"></div>
            <div class="line">
                <img src="../../assets/img/k1.jpg" alt="">
                <p>猜你喜欢</p>
            </div>
    </div>
    <div class="now-buy">
        <ul>
            <li v-for="(you,i) in like.youlike">
                <img :src="you.proImg" alt="">
                <p>{{you.proTit}}</p>
                <div class="lis-center">
                    <span>￥{{you.proPrice}}</span>
                    <p><span>{{you.proShoucang}}</span><img class="mg" src="../../assets/img/k3.jpg" alt=""></p>
                </div>
                <a class="buying" href="#">立即购买</a>
            </li>
            
        </ul>
    </div>
</div>
 </div>
</template>

<script>
 export default {
   data () {
     return {
         name:'shopCityLike'
     }
   },
  props:["shopcityData"]
 }
</script>

<style>
.like{
    position: relative;
}
.like-top{
    width:2.32rem;
    height:1px;
    background:#e1e1e1;
    display: flex;
    position: absolute;
    left:53px;
    top:-36px;
}
.line{
    display: flex;
    position: absolute;
    background:white;
    align-items: center;
    background:#efefef;
    left:120px;;
    top:-50px;
}
.line img{
    width:.28rem;
    height:.28rem;
    margin-right:10px;
}
.line p{
   font-size:14px;
   color:#999999;
}
.now-buy{
    margin-top:.81rem;
}
.now-buy ul{
    display: flex;
    width:100%;
    flex-wrap: wrap;
}
.now-buy ul li{
    margin-left: 10px;
    margin-bottom:10px;
     background:white;
}
.now-buy ul li img{
    width: 1.73rem;
    height:2.12rem;
}
.now-buy ul li p{
    width: 1.73rem;
    white-space: nowrap;
     overflow: hidden;
     text-overflow: ellipsis;
     font-size:.1rem;
     color:#313131;
    
}
.lis-center{
    width: 1.73rem;
    height:.3rem;
    align-items: center;
    display: flex;
    justify-content: space-between;
}
.lis-center span{
    font-size:.12rem;  
    margin-left: 5px;
   
}
.lis-center p{
    display: flex;
    margin-left: .7rem;
}
.lis-center p span{
    font-size:.12rem; 
}
.lis-center p .mg{
    width: .17rem;
    height:.17rem; 
}
.buying{
    width: 1.6rem;
    height:.3rem;
    background:#ff5778;
    color:white;
    font-size: .16rem;
    text-align: center;
    line-height: .35rem;
    display: block;
    border-radius: 3px;
    margin:0 auto;
}

 
</style>



