<template>
 <div class="boxs">
    <shop-city-buying></shop-city-buying>
    <shop-city-nav></shop-city-nav>
    <shop-city-show :shopcityData="shopcityData"></shop-city-show>
    <shop-city-fash :shopcityData="shopcityData"></shop-city-fash>
    <shop-city-like :shopcityData="shopcityData"></shop-city-like>
 </div>
</template>

<script>
import shopCityBuying from "./shopCityBuying"
import shopCityNav from "./shopCityNav"
import shopCityShow from "./shopCityShow"
import shopCityFash from "./shopCityFash"
import shopCityLike from "./shopCityLike"
 export default {
   data () {
     return {
         name:'shopCityBuying'
     }
   },
   components: {
       shopCityBuying,
       shopCityNav,
       shopCityShow,
       shopCityFash,
       shopCityLike
   },
   props: ["shopcityData"]
 }
</script>

<style>
 .boxs{
   width:100%;
    flex:1;
    overflow-y: auto;
}
</style>



