<template>
 <div class="BigBox">
     <my-section></my-section>
     <my-footer></my-footer>
 </div>
</template>

<script>
import mySection from "../components/my/mySection"
import myFooter from "../components/my/myFooter"
 export default {
   data () {
     return {
         name:'my'
     }
   },
   components: {
       mySection,
       myFooter
   }
 }
</script>

<style>
@import "../assets/css/zhibo.css";
 .BigBox{
    width:3.75rem;
    /* height:100%; */
    height:100%;
    display:flex;
    flex-direction: column;
  }
</style>



