<template>
 <div class="box">
     <my-header style=""></my-header>
     <my-center></my-center>
     <my-bottom></my-bottom>
 </div>
</template>

<script>
import myHeader from "./myHeader"
import myCenter from "./myCenter"
import myBottom from "./myBottom"
 export default {
   data () {
     return {
         name:'mySection'
     }
   },
   components: {
       myHeader,
       myCenter,
       myBottom
   }
 }
</script>

<style>
.box{
    /* background:url(../../assets/img/m1.png) no-repeat; */
    background-size:3.75rem 1.97rem;
    width:100%;
    flex:1;
    overflow-y: auto;
}
 
</style>



