import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import index from '@/views/index'
import shopCity from '@/views/shopCity'
import zhibo from '@/views/zhibo'
import my from '@/views/my'
import classify from '@/views/classify'
import goodsList from '@/views/goodsList'
import details from '@/views/details'
import login from '@/views/login'
import register from '@/views/register'
import shopCar from '@/views/shopCar'
Vue.use(Router)

export default new Router({
  base:"/",
  mode:"hash",
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/shopCity',
      name: 'shopCity',
      component: shopCity
    },
    {
      path: '/zhibo',
      name: 'zhibo',
      component: zhibo
    },
    {
      path: '/my',
      name: 'my',
      component: my
    },
    {
      path: '/classify',
      name: 'classify',
      component: classify
    },
    {
      path: '/goodsList',
      name: '/goodsList',
      component: goodsList
    },
    {
      path: '/details',
      name: '/details',
      component: details
    },
    {
      path: '/login',
      name: '/login',
      component: login
    },
    {
      path: '/register',
      name: '/register',
      component: register
    },
    {
      path: '/shopCar',
      name: '/shopCar',
      component: shopCar
    },
  ]
})




