<template>
 <div>
<div class="head">
        <ul>
            <li v-for="(t,index) in zhiboData" @click="change(index)" :class="{'hot':index==c}">{{t.type}}</li>
        </ul>
        
    </div>
 </div>
</template>

<script>
 export default {
   data () {
     return {
         name:'zhiboHeader'
     }
   },
   props: ["zhiboData","change","c"]
 }
</script>

<style>
.head ul{
width:100%;
height:.46rem;
display: flex;
justify-content: space-around;
align-items: center;
border-bottom: 1px solid #ededed;
}
.head ul li{
    height:.46rem;
    display: flex;
    font-size:.14rem;
    color:#191919;
    align-items: center;
}
 .head ul .hot {
    border-bottom: 2px solid #ff5777;
    color: #ff5777;
  }
</style>



