<template>
 <div class="goods-show">
     <ul>
         <li v-for="(a,index) in goodsListData[num].goods">
            <a href="index#/details">
                <img class="show-hot" :src="a.Img">
                <div class="goods-info">
                    <div class="info-top">
                        <img src="../../assets/img/hot.jpg">
                        <span>{{a.Info}}</span>
                    </div>
                    <div class="info-bottom">
                        <h3>￥{{a.price}}</h3>
                        <p><span>{{a.quantity}}W</span><img src="../../assets/img/k3.jpg"></p>
                    </div>
                </div>
            </a>
         </li>
     </ul>
 </div>
</template>

<script>
 export default {
   data () {
     return {
         name:'goodsListShow'
     }
   },
    props:["goodsListData","num"]
 }
</script>

<style>
.goods-show{
    height:100%;
    overflow-y:auto;
}
.goods-show ul{
    display:flex;
     height:100%;
    width:3.6rem;
    flex-wrap:wrap;
    margin:0 auto;
    justify-content:space-between;
}
.goods-show ul li{
    background:white;
    margin-top:10px;
}
 .goods-show ul li a .show-hot{
     width:1.76rem;
     height:2.35rem;
 }
 .info-top{
      margin-top:5px;
     display:flex;
      width:1.76rem;
      height:.33rem;
      overflow:hidden;
 }
 .info-top img{
    width:.3rem;
     height:.16rem;
 }
 .info-top span{
     font-size:.12rem;
     color:#343434;
     display:block;
 }
 .info-bottom{
     margin-top:5px;
     margin-bottom:3px;
      width:1.76rem;
     display:flex;
     justify-content:space-between;
     align-items:center;
 }
 .info-bottom h3{
     font-size:.16rem;
     color:black;
 }
 .info-bottom p span{
     color:#878787;
     font-size:.12rem;
      display:block;
 }
 .info-bottom p {
      display:flex;
      align-items:center;
 }
 .info-bottom p img{
    width:.14rem;
     height:.14rem;
     margin-left:3px;
 }
</style>



