<template>
 <div class="BigBox">
   <shop-car-header></shop-car-header>
   <shop-List :shopCarData="shopCarData" :shopCheckAll="shopCheckAll"></shop-List>
   <shop-car-footer :CheckAll="CheckAll" :zj="proChecked" :shopCarData="shopCarData"></shop-car-footer>
 </div>
</template>

<script>
import shopCarHeader from"../components/shopCar/shopCarHeader"
import shopList from "../components/shopCar/shopList"
import shopCarFooter from "../components/shopCar/shopCarFooter"
 export default {
   data () {
     return {
       name:'shopCar',
       shopCarData:[
         {
            totalPrice: 0,
            allchecked: false,
           "shop":[
              {
              "shopname":"亿万依迷",
               "checked":false,
              "shopTotalPrice":0,
              "product":[
                {
                  "Img":"static/img/car1.jpg",
                  "Info":"时尚套装女韩版修身显瘦黑色无袖背心上衣女春秋新款高腰破洞牛仔裤女宽松百搭九分直筒裤子两件套",
                  "color":"时尚套装",
                  "size":"M",
                  "price":"69.00",
                  "yprice":"98.58",
                  "num":"1",
                  "check":false
                },
                {
                  "Img":"static/img/a3.jpg",
                  "Info":"时尚套装女韩版修身显瘦黑色无袖背心上衣女春秋新款高腰破洞牛仔裤女宽松百搭九分直筒裤子两件套",
                  "color":"时尚套装",
                  "size":"s",
                  "price":"136.00",
                  "yprice":"254",
                  "num":"1",
                  "check":false
                },
              ]
            },
            {
              "shopname":"蜜桃Mini",
               "checked":false,
                "shopTotalPrice":0,
              "product":[
                {
                  "Img":"static/img/car2.jpg",
                  "Info":"春装女套装2019新款镂空很仙的蕾丝衬衫长袖羽毛上衣+打底吊带+浅色直筒九分牛仔裤三件套",
                  "color":"时尚套装",
                  "size":"M",
                  "price":"145.00",
                  "yprice":"247.00",
                  "num":"1",
                  "check":false
                }
              ]
            },
            // {
            //   "shopname":"尚美依婷",
            //   "product":[
            //     {
            //       "Img":"/static/img/gl3.jpg",
            //       "Info":"春装女套装2019新款镂空很仙的蕾丝衬衫长袖羽毛上衣+打底吊带+浅色直筒九分牛仔裤三件套",
            //       "color":"时尚套装",
            //       "size":"M",
            //       "price":"145.00",
            //       "yprice":"247.00",
            //       "num":"1"
            //     },
            //     {
            //       "Img":"/static/img/gl1.jpg",
            //       "Info":"春装女套装2019新款镂空很仙的蕾丝衬衫长袖羽毛上衣+打底吊带+浅色直筒九分牛仔裤三件套",
            //       "color":"时尚套装",
            //       "size":"M",
            //       "price":"145.00",
            //       "yprice":"247.00",
            //       "num":"1"
            //     }
            //   ]
            // }
           ]
         }
       ]
     }
   },
   components: {
     shopCarHeader,
     shopList,
     shopCarFooter
   },
    methods:{
      /**
       * 最大全选
       * */
      CheckAll(){
        let isCheck = this.shopCarData[0].allchecked;
        let shops = this.shopCarData[0].shop;//获取店铺数量
        for(let i=0;i<shops.length;i++){
          let poducts =shops[i].product;//获取店铺下对应的商品数量
          for(let j=0;j<poducts.length;j++){
            if(isCheck){
              poducts[j].check = true;//控制商品选中
              shops[i].checked = true;//控制店铺选中
            }else{
              poducts[j].check = false;//控制商品不选中
              shops[i].checked = false;//控制店铺不选中
            }
          }
        }
      },
      /**
       * 店铺全选
       * */
      shopCheckAll(i){  //此时的i是收盘cartContent组件传上来的下标值
        let isCheck = this.shopCarData[0].shop[i].checked;
        this.shopCarData[0].shop[i].product.forEach((product,index)=>{
          product.check=isCheck;
        })
      },
      // addSum(sid,pid){
      //   this.shopCartData[0].shop[sid].product[pid].num++
      // },
      // reduce(sid,pid){
      //   if(this.shopCartData[0].shop[sid].product[pid].num<=1){
      //     this.shopCartData[0].shop[sid].product[pid].num=1
      //   }else{
      //     this.shopCartData[0].shop[sid].product[pid].num--
      //   }
      // },
    },
    computed: {
      /**
       * 统计总价格
       * @param shopCartData
       */
      proChecked(data){
        let total = 0;
        let SumNum = 0;
        this.shopCarData[0].shop.forEach((shopId,i)=>{
          //   pro.checked=this.allchecked
          shopId.product.forEach((product, i) => {
              // products.checked = this.allchecked
             if(product.check){
               total += product.price * product.num;
               SumNum += product.num;
             }
            })
        })
        // console.log(SumNum)  //此接口是计算所选商品总数量之和
        return this.shopCarData[0].totalPrice = total.toFixed(2)
      },




    },
 }
</script>

<style>
@import "../assets/css/index.css";
 .BigBox{
    width:100%;
    /* height:100%; */
    height:100%;
    display:flex;
    flex-direction: column;
  }
</style>



