<template>
 <div class="goods-title">
     <ul>
         <li v-for="(g,index) in goodsListData" @click="change(index)" :class="{'now-li':index==c}">{{g.type}}</li>
     </ul>
 </div>
</template>

<script>
 export default {
   data () {
     return {
         name:'goodsListTitle'
     }
   },
    props:["goodsListData","change", "c"]
 }
</script>

<style>
.goods-title ul{
    display:flex;
    width:100%;
    height:.4rem;
    background:white;
    align-items:center;
}
.goods-title ul li{
    font-size:.14rem;
    width:.95rem;
    text-align:center;
    line-height:.17rem;
    height:.17rem;
    color:#373737;
    border-right:1px solid #dedede;

}
.goods-title ul .now-li{
    color:#ce395d;
}
 
</style>



