<template>
 <div class="box">
     <goods-list-title :goodsListData="goodsListData" :change="change" :c="c"></goods-list-title>
     <goods-list-show :goodsListData="goodsListData" :num="num"></goods-list-show>
 </div>
</template>

<script>
import goodsListTitle from "./goodsListTitle"
import goodsListShow from "./goodsListShow"
 export default {
   data () {
     return {
         name:'goodsListSection',
         num:0,
         c:0
     }
   },
   methods:{
       change(index){
           this.num=index;
           this.c=0;
           this.c=index;
       }
   },
   components: {
       goodsListTitle,
       goodsListShow
   },
    props:["goodsListData"]
 }
</script>

<style>
.box{
    height:100%;
}
 
</style>



