<template>
 <div>
<div class="head-word">
            <ul>
                <li>
                    <img src="../../assets/img/m6.jpg" alt="">
                    <span>购物车</span>
                     <span>></span>
                </li>
                <li>
                    <img src="../../assets/img/m7.jpg" alt="">
                    <span>我的订单</span>
                    <span>></span>
                </li>
                <li>
                    <img src="../../assets/img/m8.jpg" alt="">
                    <span>优惠券</span>
                    <span>></span>
                </li>
            </ul>
        </div>
 </div>
</template>

<script>
 export default {
   data () {
     return {
         name:'myCenter'
     }
   },
   components: {

   }
 }
</script>

<style>

 .head-word{
    margin-top:2rem;
    background:white;
}
.head-word ul li{
    display: flex;
    align-items: center;
    margin-left: .2rem;
    width:3.55rem;
    height:.56rem;
    border-bottom:1px solid #d3d3d3;
    position:relative;
}
.head-word ul li:last-of-type{
    border-bottom:none;
}
.head-word ul li img{
    width:.19rem;
    height:.19rem;
    margin-left: 10px;
}
.head-word ul li span{
    font-size: .16rem;
    color:#696969;
    margin-left: 10px;
}
 .head-word ul li span:nth-of-type(2){
     position:absolute;
     right:10px;
     top:15px;
 }
</style>



