<template>
 <div  >
<div class="show" v-for="(look,i) in shopcityData">
    <img src="../../assets/img/w1.jpg" alt="">
    <div class="three"></div>
    <ul class="shop-show"  v-for="(l,i) in look.look">
        <a href="#" v-for="(pro,i) in l.lookProductd">
            <li >
               <img :src="pro.lookImg" alt="">
                <p>{{pro.lookTit}}</p>
                <span>{{pro.lookInfo}}</span>
            </li>
        </a>
    </ul>
   </div>
 </div>
</template>

<script>
 export default {
   data () {
     return {
         name:'shopCityShow'
     }
   },
    props:["shopcityData"]
 }
</script>

<style>
.show{
    position: relative;
    margin-bottom:10px;
}
.show img{
    width:100%;
    height:1.3rem;
    display: block;
}
.shop-show{
    display: flex;
    height:2.39rem;
    width:100%;
    background:white;
    flex: 1;
    overflow-x: auto
}
.three{
    width:0;
    height:0;
    border:10px solid transparent;
    border-bottom-color: white;
    position: absolute;
    left: 22px;
    top:111px;
    z-index:10;
}
.shop-show a{
    color:transparent;
    display: block;
    flex-shrink: 0;
    margin-top:10px;
}
.shop-show a li img{
    width:1.12rem;
    height:1.5rem;
    margin-left: .1rem;
    border-radius: 5px;
}
.shop-show a li p{
    color:black;
font-size: 14px;
text-align: center;
margin-top:.1rem;
}
.shop-show a li span{
    font-size: 12px;
    text-align: center;
    display: block;
    margin-top:5px;
    color:#9f9f9f;
}
 
</style>



